export async function processTestImages(files) {
    console.log('Number of image files found:', files.length);
    // Create a scheduler and workers
    const scheduler = createScheduler();

    const workerGen = async () => {
        const worker = await createWorker("eng", 1, { 
            logger: m => console.log(m), cachePath: ".",
            workerPath: chrome.runtime.getURL('node_modules/tesseract.js/dist/worker.min.js'),
            corePath: chrome.runtime.getURL('node_modules/tesseract.js-core/tesseract-core-lstm.wasm.js'),
            workerBlobURL: false,
        });
        await worker.setParameters({
            tessedit_pageseg_mode: PSM.SPARSE_TEXT_OSD
        });
        scheduler.addWorker(worker);
    };

    const workerN = 7; //Adjustable worker pool for a huge batch
    await Promise.all(Array(workerN).fill(0).map(async () => await workerGen())); // Create workers concurrently

    // Process images in parallel
    console.log('Processing images and performing OCR:');
    
    const jobPromises = files.map(async (file) => {
        console.log(`Scheduling image processing for: ${file.name}`);

        return scheduler.addJob('recognize', file, {}, {
            text: true,
            blocks: true,
            hocr: false,
            tsv: false,
        })
        .then(out => {
            const wordsArray = [];
            if (!out.data?.blocks) {
                console.warn(` No blocks found in OCR result for: ${file.name}`);
                console.log(out)
                return {
                    imageName: file.webkitRelativePath || file.name,
                    words: []
                };
            }
            // Ensure blocks exist before iterating
            if (out.data?.blocks) {
                console.log("blocks have been ensured")
                out.data.blocks.forEach((block, blockIdx) => {
                    block.paragraphs.forEach((para, paraIdx) => {
                        const paragraphKey = `block${blockIdx}_paragraph${paraIdx}`;

                        para.lines.forEach((line, lineIdx) => {
                            const lineKey = `${paragraphKey}_line${lineIdx}`;

                            line.words.forEach(word => {
                                wordsArray.push({
                                    word_text: word.text,
                                    word_confidence: word.confidence,
                                    word_choices: word.choices,
                                    word_bbox: word.bbox,
                                    unique_paragraph_key: paragraphKey,
                                    unique_line_key: lineKey
                                });
                            });
                        });
                    });
                });
            }
            return {
                imageName: file.webkitRelativePath || file.name,
                words: wordsArray
            };
        })
        .catch(error => ({
            imageName: file.webkitRelativePath || file.name,
            error: error.message,
        }));
    });

    const results = await Promise.all(jobPromises);
    // Terminate workers and save results
    await scheduler.terminate();

    console.log('OCR processing completed.');
    return results;

}

export async function processTestImagesReg(files) {
    console.log(`filename is ${files})`)
    try {
        if (!files) {
            throw new Error("No files provided for OCR.");
        }

        const imageFiles = Array.from(files).filter(file =>
            file.type.startsWith('image/')
        );

        if (imageFiles.length === 0) {
            throw new Error("No image files found in selected folder.");
        }

        console.log(` Folder contains ${imageFiles.length} image file(s).`);

        const scheduler = createScheduler();
        const workerCount = 7;

        console.log(' Initializing workers...');
        const workerPromises = [];

        for (let i = 0; i < workerCount; i++) {
            workerPromises.push(
                (async () => {
                    try {
                        const worker = await createWorker('eng', 1, {
                            logger: m => console.log(`Worker ${i + 1}:`, m),
                            workerPath: chrome.runtime.getURL('node_modules/tesseract.js/dist/worker.min.js'),
                            corePath: chrome.runtime.getURL('node_modules/tesseract.js-core/tesseract-core.wasm.js'),
                            workerBlobURL: false,
                        });
                        scheduler.addWorker(worker);
                    } catch (err) {
                        console.error(`Error creating worker ${i + 1}:`, err);
                    }
                })()
            );
        }

        await Promise.all(workerPromises);
        console.log(` ${workerCount} workers ready.`);

        const jobPromises = imageFiles.map(async (file) => {
            console.log(`Scheduling image processing for: ${file}`);
            return scheduler.addJob('recognize', file, {rotateAuto: true,})
                .then(out => ({
                    imageName: file.webkitRelativePath || file.name,
                    words: out.data.words
                }))
                .catch(error => ({
                    imageName: file.webkitRelativePath || file.name,
                    error: error.message,
                }));




        });

        const results = await Promise.all(jobPromises);
        console.log(' OCR complete for all image files.', results);

        await scheduler.terminate();
        console.log(' Scheduler terminated. Workers cleaned up.');

        return results;

    } catch (err) {
        console.error(' Error in processTestImages:', err);
        throw err;
    }
}